import React from 'react';
import Select from 'react-select';
import { navigate } from "gatsby"

const options = [
  { value: 'cnpc-wuzhong-road', label: '- CNPC, Wuzhong Road' },
  { value: 'shanghai-international-commerce-center', label: ' - ICC (Shanghai International Commerce Center)' },
  { value: 'intime-hangzhou', label: ' - Intime Hangzhou' },
  { value: 'k11-art-mall', label: 'K11 Art Mall' },
  { value: 'lilacs-international-commercial-center', label: ' - Lilacs International Commercial Center' },
  { value: 'peoples-square', label: ' - People\'s Square' },
  { value: 'shanghai-hongqiao-international-airport', label: ' - Shanghai Hongqiao International Airport' },
  { value: 'shanghai-pudong-international-airport', label: ' - Shanghai Pudong International Airport' },
  { value: 'sinopec-jinxiu-road', label: '- Sinopec, Jinxiu Road' },
  { value: 'unielite-shopping-mall', label: ' - UniElite Shopping Mall' },
  { value: 'xtd-plaza', label: ' - XTD Plaza' },
];

class Form extends React.Component {
  state = {
    selectedOption: null,
  };
  handleChange = selectedOption => {
    this.setState({ selectedOption });
    navigate(`/en/locations/${selectedOption.value}/`)
  };

  render() {
    const { selectedOption } = this.state;

    return (
      <Select
        value={selectedOption}
        placeholder={'Select a location'}
        onChange={this.handleChange}
        options={options}
        clearable={false}
      />
    );
  }
}

export default Form
